import React, { useState, useEffect } from 'react'
import { Link, useNavigate, useSearchParams } from 'react-router-dom'

const locationData = [
  {
    name: "H Ch Minh"
  },
  {
    name: "H Ni"
  },
  {
    name: "Ninh Bnh"
  },
  {
    name: " Nng"
  },
  {
    name: "ng Nai"
  }
]
const Sidebar = () => {
  const navigate = useNavigate();

  const [searchParams] = useSearchParams();

  const [locationState, setLocationState] = useState();

  const handleSortLocation = (location) => setLocationState(location)

  useEffect(() => {
    if (locationState) {
      navigate({
        pathname: '/product-page',
        search: `?category=${searchParams.get("category")}&location=${locationState}`,
      });
    }
  }, [locationState])

  return (
    <div className="sidebarContainer">
      {/* Danh mc sn phm */}
      <div className="block">
        <h4>DANH MC SN PHM</h4>
        <div className='sidebarCategories'>
          <Link to="#">Thit B m Thanh v Ph Kin</Link>
          <Link to="#">Thit B Chi Game v Ph Kin</Link>
          <Link to="#">Thit B eo Thng Minh v Ph Kin</Link>
          <Link to="#">Thit B Thng Minh v Linh Kin in</Link>
          <Link to="#">Ph Kin in Thoi v My Tnh Bn</Link>
          <Link to="#">Ph Kin My Tinh v Laptop</Link>
        </div>
      </div>
      {/* Ni bn */}
      <div className="block" style={{ borderTop: '1px solid rgb(247, 247, 247)' }}>
        <h4>Ni bn</h4>
        <div className='sidebarLocation'>
          {locationData && locationData.length > 0 && locationData.map((item) => {
            return (
              <label key={item.name}>
                <input type="checkbox" onChange={() => handleSortLocation(item.name)} />
                <div>{item.name}</div>
              </label>
            )
          })}
        </div>
      </div>
    </div>
  )
}

export default Sidebar